.input-radio {
  padding: 1px 0;
  padding-left: 76px;
  min-height: 56px;
  display: block;
  display: flex;
  align-items: center;
}
.input-radio__elm {
  opacity: 0;
  position: absolute;
  z-index: -1;
}
.input-radio__indicator {
  margin-left: -76px;
  float: left;
  border: 17px solid #f2f2f2;
  width: 22px;
  height: 22px;
  line-height: 22px;
  border-radius: 100%;
  display: inline-block;
  position: relative;
  transition: 200ms ease-in-out;
  background-color: #f2f2f2;
  vertical-align: middle;
}
.input-radio__elm:hover ~ .input-radio__indicator {
  background-color: #e5e5e5;
  border-color: #e5e5e5;
}
.input-radio__elm:active ~ .input-radio__indicator,
.input-radio__elm:focus ~ .input-radio__indicator {
  background-color: #d5d5d5;
  border-color: #d5d5d5;
}
.input-radio__elm:checked ~ .input-radio__indicator {
  background-color: #333;
  border-color: #f2f2f2;
}
.input-radio__elm:checked:hover ~ .input-radio__indicator {
  background-color: #333;
  border-color: #e5e5e5;
}
.input-radio__elm:checked:active ~ .input-radio__indicator {
  background-color: #a4a4a4;
  border-color: #d5d5d5;
}
.input-checkbox {
  padding: 1px 0;
  padding-left: 76px;
  min-height: 56px;
  display: flex;
  align-items: center;
}
.input-checkbox__elm {
  opacity: 0;
  position: absolute;
  z-index: -1;
}
.input-checkbox__indicator {
  margin-left: -76px;
  float: left;
  width: 56px;
  height: 56px;
  line-height: 56px;
  border-radius: 5px;
  display: inline-block;
  position: relative;
  transition: 200ms ease-in-out;
  background-color: #f2f2f2;
  vertical-align: middle;
}
.input-checkbox__indicator::after {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 21px;
  content: "\e906";
  display: none;
  position: absolute;
  width: 56px;
  height: 56px;
  line-height: 56px;
  text-align: center;
}
.input-checkbox__elm:hover ~ .input-checkbox__indicator {
  background-color: #e5e5e5;
}
.input-checkbox__elm:active ~ .input-checkbox__indicator,
.input-checkbox__elm:focus ~ .input-checkbox__indicator {
  background-color: #d5d5d5;
}
.input-checkbox__elm:checked ~ .input-checkbox__indicator {
  background-color: #f2f2f2;
}
.input-checkbox__elm:checked ~ .input-checkbox__indicator::after {
  display: block;
  color: #333;
}
.input-checkbox__elm:checked:hover ~ .input-checkbox__indicator {
  background-color: #e5e5e5;
}
.input-checkbox__elm:checked:hover ~ .input-checkbox__indicator::after {
  color: #333;
}
.input-checkbox__elm:checked:active ~ .input-checkbox__indicator {
  background-color: #d5d5d5;
}
